@charset "utf-8";

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

* {
  transition: all 0.25s ease-in-out;
}

.container {
  width: min(100% - 30px, 1080px);
  margin-inline: auto;
}

.flex {
  display: flex;
}

.spacebetween {
  justify-content: space-between;
}

.center {
  display: grid;
  place-items: center;
}

body {
  font-family: 'Nunito', sans-serif, Arial, sans-serif;
  background-color: #181720;
}

.hidden {
  display: none;
}

ul, .gap {
  gap: 20px;
}

section + section {
  margin-top: 80px;
}

h1, h2, h3, p, li, i, button {
  color: #fff;
}

h2 {
  font-size: 30px;
}

/*
  Header
*/

header {
  height: 60px;
  background-color: #005e97;
}

h1 {
  font-size: 26px;
  font-weight: 500;
  line-height: 60px;
}

nav ul li {
  line-height: 60px;
  list-style: none;
}

li {
  font-size: 18px;
  font-weight: 600;
  text-decoration: none;
  cursor: pointer;
}

nav ul li:hover, .links i:hover {
  color: #d67514;
}

/*
  Content
*/

/* Banner */

.banner {
  height: calc(100svh - 60px);
  background-image: linear-gradient(175deg, #005e97, #181720);
}

.banner h2 {
  font-size: 38px;
  font-weight: 700;
}

.title {
  color: #d67514;
}

.article {
  width: 700px;
  margin-bottom: 20px;
}

.article p {
  font-size: 18px;
  margin-top: 10px;
  text-align: center;
}

button {
  width: 140px;
  height: 36px;
  font-size: 16px;
  font-weight: 500;
  border-radius: 6px;
  border: none;
  cursor: pointer;
}

button + button {
  margin-left: 20px;
}

.primary {
  background-color: #d67514;
}

.default {
  background-color: #181720;
}

.primary:hover {
  background-color: #fff;
  color: #d67514;
  scale: 1.02;
}

.default:hover {
  background-color: #fff;
  color: #181720;
  scale: 1.02;
}


/* Movies */

.rating {
  color: #d67514;
}

.movie-container {
  margin-top: 12px;
}

.movie-box {
  width: 200px;
}

.movie-box h3 {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.movie-box p {
  margin: 6px 0;
}

img {
  width: 200px;
  cursor: pointer;
  border-radius: 6px;
}

img:hover {
  scale: 1.02;
  border-radius: 0;
}

.review {
  display: -webkit-box;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical; /*got from chatGPT*/
  overflow: hidden;
  text-overflow: ellipsis;
}

/*
  Footer
*/

footer {
  margin-top: 100px;
  height: 100px;
  background-color: #16192d;
}

.end {
  line-height: 100px;
}

.links ul li {
  list-style: none;
}

.links i {
  font-size: 20px;
}

.copyright {
  font-size: 17px;
}